import * as React from "react"
import Layout from "../components/Layout";
import "../css/events-page.scss";
import {useEffect} from "react";
import {Trans as T, useI18next, useTranslation} from 'gatsby-plugin-react-i18next';
import {graphql} from "gatsby";
import * as PropTypes from "prop-types";
import {Translation} from 'react-i18next';

const EventsPage = () => {
    const [dogs, setDogs] = React.useState([]);
    const dagsRef = React.useRef([]);


    useEffect(() => {
        if (!dagsRef.current.length) {
            fetch('https://dog.ceo/api/breed/pomeranian/images/random/20')
                .then(res => res.json())
                .then(result => setDogs(result.message));
        }
        // if (!dogs.length) {
        //     fetch('https://dog.ceo/api/breed/pomeranian/images/random/20')
        //         .then(res => res.json())
        //         .then(result => setDogs(result.message));
        // }
    })

    const dogPhotos = dagsRef.current.map((item) => <img src={item} alt="" key={item}/>)

    return (<Translation>
            {t => (
                <Layout>
                    <div className="events-page__intro">
                        <h4>
                            {t('events_page.dogs_dialog')}
                        </h4>
                    </div>
                    <div className="wrapper">
                        <div className="events-page__photos">
                            {dogPhotos}
                        </div>
                    </div>
                </Layout>)}
        </Translation>
    )
};

export default EventsPage;

export const query = graphql`
   query($language: String!) {
    locales: allLocale(filter: {language: {eq: $language}, ns: {in: ["events-page", "common"]}}) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;